import React from 'react'
import CatNavbar from '../components/CatNavbar'
import "../css/who.css"
import CatProfile from '../components/CatProfile';
import CatSleeping from '../components/CatSleeping';
import StudiesCard from '../components/StudieCard';
import logo from "../images/pfp.png";
import uge from "../images/uge.png";
import vdg from "../images/vdg.png";
import caseware from "../images/caseware.png";
import cv from "../images/CV_2024-06-28_NIDAL_IDRISSI.pdf"

import axios from "../images/axios.png";
import react from "../images/react.png";
import ts from '../images/ts.png';
import js from "../images/js.png";
import java from "../images/java.png";
import php from "../images/php.png";
import python from "../images/py.png";
import sc from "../images/styled-components.png"

import jira from "../images/jira.png";
import gh from "../images/github.svg";
import figma from "../images/figma.svg";
import sb from "../images/scenebuilder.png";
import android from "../images/android.jpg";

const Who = ({ setIsVisible }) => {

  React.useEffect(() => {
    setIsVisible(true);
  });

  let data = {
    comms : [
      "Actuellement grand admissible  l'cole d'ingnieur ESIEE Paris, je recherche une alternance de 36 mois dans le domaine de l'informatique.",
    ],
    intro : [
      "Je suis un tudiant de 19 ans en troisime anne d'un BUT en informatique, j'ai une passion profonde pour le dveloppement front-end, la conception UX et l'art.",
      "Je prends plaisir  dvelopper des sites web interactifs, intuitifs et visuellement intressants, souvent en React. J'aime galement jouer  des jeux vido, et mes deux prfrs sont Persona 5 et Zelda. Je trouve que ces jeux offrent non seulement un moyen amusant et engageant de se dtendre, mais ils m'inspirent galement  penser de manire crative et  sortir des sentiers battus en matire de conception et de rsolution de problmes."
    ],
    introTitle : "Qui suis-je ?",
    skillsTitle : "Mes Comptences",

    studies : [
      {
        title : "Caseware France",
        text : "Premire exprience professionelle dans cette entreprise en alternance. Mes missions : dvelopper et maintenir des applications cloud et desktop visant  aider les auditeurs dans leur travail.",
        date : "2022/2024",
        logo : caseware,
      },
      {
        title : "IUT de Marne la valle",
        text : "C'est dans cet tablissement que j'ai appris l'informatique et la vie d'entreprise grace  l'alternance. Etant en tte de promotion, je souhaite intergrer une grande cole d'ingnieur par la suite.",
        date : "2021/2024",
        logo : uge,
      },
      {
        title : "Concours de mathmatiques",
        text : "Pendant mon anne de terminale, moi et mes camarades avions remport un concours de maths. Nous avions conu un objet permettant  des personnes malvoyantes, sourdes et muettes de faire des calculs et de les partager entre eux sans difficults. Cet objet avait pour but premier de facilter les cours des professeurs.",
        date : "2020/2021",
        logo : vdg,
      },
      {
        title : "Lyce Van Dongen - Mention Bien",
        text : "J'ai tudi et obtenu mon bac avec mention dans ce lyce en filire gnrale avec comme spcialits les maths, la physique/chimie et la science de l'ingnieur ainsi qu'une option art.",
        date : "2018/2021",
        logo : vdg,
      }
    ],

  }

  return (
    <div className='whoCont'>
      <div className='WhoIntroCont'>
        <div className='WhoIntroImg'>
          <CatProfile />
        </div>

        <div className='WhoIntroText'>
          <h2 className='WhoIntroTextTitle'>
            { data.introTitle }
          </h2>
          {
            data.comms.map((item, i) => {
              return (
                <div className='WhoIntroTextComm' key={i}>
                  {item}
                </div>
              );
            })
          }
          {
            data.intro.map((item, i) => {
              return (
                <p key={i}>
                  {item}
                </p>
              );
            })
          }
          <p>
            <a href={cv} target={"_blank"}>Lire mon CV</a>
          </p>
        </div>
      </div>

      <div className='WhoSkillsCont'>
        <CatSleeping />
        <div className='WhoSkillsContent'>
          <h2 className='WhoSkillsTextTitle'>
            { data.skillsTitle }
          </h2>

          <div className='WhoSkillsList'>
            <div className='WhoSkillCont'>
              <h3 className='WhoSkillTitle'>
                Techno
              </h3>

              <ul className='WhoSkillList'>
                <li className='WhoSkillImg'>
                  <img src={react} alt='React' />
                </li>
                <li className='WhoSkillImg'>
                  <img src={ts} alt='Typescript' />
                </li>
                <li className='WhoSkillImg'>
                  <img src={js} alt='Javascript' />
                </li>
                <li className='WhoSkillImg'>
                  <img src={sc} alt='styled-components' />
                </li>
                <li className='WhoSkillImg'>
                  <img src={axios} alt='Axios' />
                </li>
                <li className='WhoSkillImg'>
                  <img src={php} alt='PHP' />
                </li>
                <li className='WhoSkillImg'>
                  <img src={python} alt='Python' />
                </li>
                <li className='WhoSkillImg'>
                  <img src={java} alt='Java' />
                </li>
              </ul>   
            </div>

            <div className='WhoSkillCont'>
              <h3 className='WhoSkillTitle'>
                  Logiciels
              </h3>

              <ul className='WhoSkillList'>
                <li className='WhoSkillImg'>
                  <img src={figma} alt='figma' />
                </li>
                <li className='WhoSkillImg'>
                  <img src={gh} alt='Github' />
                </li>
                <li className='WhoSkillImg'>
                  <img src={sb} alt='Scene builder' />
                </li>
                <li className='WhoSkillImg'>
                  <img src={android} alt='android studio' />
                </li>
                <li className='WhoSkillImg'>
                  <img src={jira} alt='jira softwares' />
                </li>
              </ul>  
            </div>

            <div className='WhoSkillCont'>
              <h3 className='WhoSkillTitle'>
                  Autres
              </h3>

              <ul className='WhoSkillClassicList'>
                <li>TOEIC : 970 / 990</li>
                <li>+10 ans de dessin</li>
                <li>Exprience en montage video</li>
                <li>Je sais rsoudre un rubiks cube en 2 minutes</li>
              </ul>  
            </div>
          </div> 
        </div>
      </div>

      <div className='WhoStudiesCont'>
        <h2 className='WhoStudiesTitle'>Scolarit / Pro</h2>
      {
            data.studies.map((item, i) => {
              return (
                <StudiesCard logo={item.logo} title={item.title} date={item.date} text={item.text} />
              )
            })
          }    
      </div>
    </div>
  )
}

export default Who
